import React, { Component } from "react";
import {
  Revealed1,
  Revealed2,
  Revealed3,
  Revealed4,
  Revealed5,
  Revealed6
} from "./Reveal";
import Link_pic from "../Pics/link_go.png";

class MazeRunner extends Component {
  constructor() {
    super();
    this.state = {
      clicked: this.clicked,
      hover: "0vh"
    };
  }

  inside_ = () => {
    this.setState({
      hover: "15vh"
    });
  };

  outside_ = () => {
    this.setState({
      hover: "0vh"
    });
  };

  clicked1_ = () => {
    this.setState({
      clicked: !this.state.clicked
    });
  };

  render() {
    const { clicked } = this.state;
    return (
      <div onClick={this.clicked1_}>
        {clicked ? (
          <div className="proj-container">
            <div className="proj-hover">
              <div className="middle-content">
                <p>
                  Program that generates a random maze and solves it using DFS
                  algorithm
                </p>
                <ul>
                  <li>CSc 2720 Data Structures</li>
                  <li>Eclipse - Java</li>
                </ul>
              </div>
              <div className="end-links">
                <p style={{ fontSize: "2vh" }}>
                  Fall 2016 <br />
                  <span className="end-links-pic">
                    <a
                      href="https://github.com/ahsia3/MazeRunner"
                      target="_blank"
                    >
                      <img
                        src={Link_pic}
                        alt="click link"
                        width="25px"
                        height="25px"
                      ></img>
                    </a>
                  </span>
                </p>
              </div>
            </div>
            <p
              className="proj-title-bg"
              style={{
                letterSpacing: "2vh"
              }}
            >
              MazeRunner
            </p>
          </div>
        ) : (
          <div
            className="proj-hover"
            style={{
              fontSize: "5vh",
              letterSpacing: "2vh",
              marginLeft: `${this.state.hover}`
            }}
            onMouseEnter={this.inside_}
            onMouseLeave={this.outside_}
          >
            <Revealed1>MazeRunner</Revealed1>
          </div>
        )}
      </div>
    );
  }
}

export default MazeRunner;

