import React, { Component } from "react";
import MazeRunner, {
  WASDodge,
  CHAINVOTE,
  THELOSTCORE,
  FORGECLICKER,
  SUPERSURVIVOR,
  SECURESAY,
  PIZZA,
  WARPPA
} from "./proj-content";
import { RevealOnly } from "./Reveal";

class projects extends Component {
  constructor() {
    super();
    this.state = {
      clicked1: false,
      content_num: 1,
      hover: "0px",
      hover_less: "0px"
    };
  }

  back_content = () => {
    if (this.state.content_num === 1) {
      this.setState({
        content_num: 1
      });
    } else {
      this.setState({
        content_num: this.state.content_num - 1
      });
    }
  };

  reload_content = () => {
    if (this.state.content_num === 3) {
      this.setState({
        content_num: 3
      });
    } else {
      this.setState({
        content_num: this.state.content_num + 1
      });
    }
  };

  hover_in = () => {
    this.setState({
      hover: "20px"
    });
  };
  hover_out = () => {
    this.setState({
      hover: "0px"
    });
  };

  hover_inless = () => {
    this.setState({
      hover_less: "20px"
    });
  };
  hover_outless = () => {
    this.setState({
      hover_less: "0px"
    });
  };

  render() {
    return (
      <div className="flex-container">
        <div
          className="load-prev"
          onClick={this.back_content}
          onMouseEnter={this.hover_inless}
          onMouseLeave={this.hover_outless}
          style={{ cursor: "pointer" }}
        >
          {this.state.content_num === 1 ? (
            <div className="load-prev" style={{ cursor: "default" }}>
              Projects
            </div>
          ) : (
            <div
              style={{
                marginTop: `${this.state.hover_less}`
              }}
            >
              prev
            </div>
          )}
        </div>

        <div className="content-main-proj">
          {this.state.content_num === 1 ? (
            <ul className="content-main-proj-ul">
              <li>
                <MazeRunner clicked={this.state.clicked1}></MazeRunner>
              </li>

              <li>
                <WASDodge clicked={this.state.clicked1}></WASDodge>
              </li>
              <li>
                <CHAINVOTE clicked={this.state.clicked1}></CHAINVOTE>
              </li>
              <li>
                <THELOSTCORE clicked={this.state.clicked1}></THELOSTCORE>
              </li>
              <li>
                <FORGECLICKER clicked={this.state.clicked1}></FORGECLICKER>
              </li>
              <li>
                <SUPERSURVIVOR clicked={this.state.clicked1}></SUPERSURVIVOR>
              </li>
            </ul>
          ) : this.state.content_num === 2 ? (
            <ul className="content-main-proj-ul">
              <li>
                <SECURESAY clicked={this.state.clicked1}></SECURESAY>
              </li>
              <li>
                <PIZZA clicked={this.state.clicked1}></PIZZA>
              </li>
              <li>
                <WARPPA clicked={this.state.clicked1}></WARPPA>
              </li>
            </ul>
          ) : (
            <div>To be continued...</div>
          )}
        </div>
        <div
          className="load-next"
          onClick={this.reload_content}
          onMouseEnter={this.hover_in}
          onMouseLeave={this.hover_out}
        >
          <div style={{ marginBottom: `${this.state.hover}` }}>more</div>
        </div>
      </div>
    );
  }
}

export default projects;
